@import '../../styles/contants.scss';

.wrapper {

    position: relative;
    z-index: 1000;
    opacity: 1;
    transform: none;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transition: all 300ms ease;
    background-color: #F8F7F1;


    .container {
        position: relative;
        justify-content: space-between;
        font-weight: 500;

        .name {
            font-size: 1.5rem;
            font-weight: 500;

            a {
              text-decoration: none;
              color: $primary;
              &:hover svg {
                transform: scale(1.1);
              }
            } svg {
              font-size: 2rem;
              margin-right: 1rem;
              transition: .1s linear;

            }

        }

        .menu {
            
            @media (max-width: $sm), (max-width: $md) {
                position: absolute;
                list-style: none;
                gap: 2rem;
                font-weight: 500;
                flex-direction: column;
                right: 0;
                top: 3rem;
                width: 50%;
                min-width: 18rem;
                background: #fff;
                padding: 2rem;
                display: flex;
                border-radius: 2rem;
                transition: all 300ms ease;
                align-items: flex-start;
                padding: 3rem;
                justify-content: flex-end;
                box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.05);
            }


            text-transform: uppercase;
            font-size: 1rem;
            list-style: none;
            gap: 1.5rem;
            cursor: pointer;

            li {
                a {
                    color: inherit;
                    text-decoration: none;
                    transition: .2s linear;

                }
                &:hover {

                    @media (max-width: $sm), (max-width: $md) {
                      transform: translateY(0);
                    }
                    color: $secondary;
                    transform: translateY(-6px);
                    transition: .2s linear;

                }
            }

            .phone {
                gap: 1.2rem;
                flex-wrap: wrap;


                svg {
                    color: $secondary;
                    background-color: #fff;
                    padding: 10px;
                    border-radius: 999px;
                    box-shadow: 0 4px 4px rgba(0,0,0,0.1);
                }
            }
        }

        .burger {
            display: block;
            position: relative;
            margin-top: 8px;
            cursor: pointer;
            user-select: none;
            appearance: none;
            border: none;
            outline: none;
            background: none;
            z-index: 100;
            display: none;
          
            span {
              margin-bottom: 5px;
              width: 35px;
              height: 2px;
              background-color: black;
              display: block;
              transition: transform .2s ease-in-out;
              -webkit-transition: transform .2s ease-in-out;
            }

            // &:hover span:nth-child(2) {
            //     transform: translateX(15px);
            //     background: #800080;
            // }
          
            &.is-active span {
              background-color: #800080;
              transition: transform .2s ease-in-out;
            }
          
            &.is-active:hover span {
              background-color: #800080;
            }
          
            &.is-active span:nth-child(1) {
              transform: rotate(-135deg) translate(-3px, -7px);
            }
          
            &.is-active span:nth-child(2) {
              transform: translateX(-15px);
              opacity: 0;
            }
          
            &.is-active span:nth-child(3) {
              transform: rotate(135deg) translate(-3px, 7px);
            }

            @media (max-width: $sm), (max-width: $md) {
                display: block;
            }


          }
          
        
    }
}